.figureclass {
    position: relative;
    overflow: hidden;
    cursor: pointer;
  }
.figureclass img{
    transition: all 500ms ease-out;
    height: 100%;
  }
.figureclass .capa{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(10, 10, 10, 0.7);
    transition: all 500ms ease-out;
    opacity: 0;
    visibility: hidden;
    text-align: center;
  }
.figureclass:hover > .capa{
    opacity: 1;
    visibility: visible;
  }
.figureclass:hover > .capa .tituloalbum{
   font-size: 20px;
  }
.figureclass:hover > .capa .descripcionalbum{
    font-size: 15px;
   }
.figureclass:hover > .capa .precioalbum{
    font-size: 15px;
   }
  
.figureclass .capa .tituloalbum{
    color: #ffffff;
    margin-bottom: 120px;
    transition: all 500ms ease-out;
    margin-top: 60px
  }
.figureclass .capa .descripcionalbum{
    color: #ffffff;
    margin-bottom: 30px;
    transition: all 500ms ease-out;
    margin-top: 10px
  }
.figureclass .capa .precioalbum{
    color: #ffffff;
    margin-bottom: 5px;
    transition: all 500ms ease-out;
    margin-top: 15px
  }
